import * as React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import VideoCard from '../../components/Card'
import Header from '../../components/header'
import Footer from '../../components/footer'
import jsonLink from './link.json'


function Home() {

  let [linkVideo, setLinkVideo] = React.useState([])


  React.useEffect(() => {
    let vids = []
    fetch('./link.json')
      .then(res => {
        if(!res.ok){
          throw Error(res.statusText);
        }
        return res.json();
      })
      .then(() => {
        console.log('links', jsonLink)
        for (let i = 0; i < jsonLink.length; i++) {
          vids.push(
            <VideoCard
              link={jsonLink[i].link}
              desc={jsonLink[i].desc}
            />
          )
        }
        console.log('vids', vids)
        setLinkVideo(vids)
      })
      .catch(error => console.error(error))
  }, [])


  return (
    <main role="main">
      <Header />

      <section className="jumbotron text-center">
        <div className="container">
          <h1 className="jumbotron-heading">Experience VR</h1>
          <p className="lead text-muted">Browse through our collection of various types of Virtual Reality videos of your liking.</p>
          <p>
            <a href="#" className="btn btn-primary my-2">Main call to action</a>
            <a href="#" className="btn btn-secondary my-2">Secondary action</a>
          </p>
        </div>
      </section>

      <div className="album py-5 bg-light">
        <div className="container">
          <div className="row">
       
            {linkVideo.length !== 0 ? linkVideo : <div>No video links found</div>}
                
          </div>
        </div>
      </div>

      <Footer />
    </main>
  )
}

export default Home